.search {
  width: 18%;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 1.2rem;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  margin: 2rem auto 1.2rem auto;
}

h1.logo {
  font-size: 1.25rem;
}

input {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  height: 1.8rem;
  border: 0;
  color: rgba(255, 255, 255, 1);
  width: 100%;
  padding: 0.5rem;
}

input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

input:focus {
  background: rgba(255, 255, 255, 0.3);
}

@media screen and (max-width: 768px) {
  .header {
    z-index: 1;
  }

  .header__wrapper {
    margin: 1.5rem auto 1rem auto;
  }

  .nav__list {
    flex-wrap: wrap;
  }

  .search {
    width: 30%;
  }
}

@media screen and (max-width: 550px) {
  h1.logo {
    margin: 1rem 0;
  }

  .search {
    width: 40%;
  }
}

@media screen and (max-width: 450px) {
  h1.logo {
    font-size: 1rem;
  }
}
