.footer__wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px 0;
  font-size: 1rem;
}

.footer__info {
  display: flex;
  gap: 20px;
}

.footer__copyright {
  display: flex;
  align-items: center;
}

.year {
  padding: 0 20px;
}

.github-username,
.rss {
  text-decoration: none;

  transition: 0.3s;
}

@media screen and (max-width: 768px) {
  .footer__wrapper {
    flex-wrap: wrap;
    grid-gap: 20px;
  }
}
