.card {
  max-width: 100%;
  display: flex;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  flex-direction: column;
}

.card__img img {
  height: auto;
  width: 100%;
  border-radius: 8px 8px 0 0;
}

.card__props {
  display: flex;
  justify-content: flex-start;
  gap: 2rem;
}

.card__info {
  padding: 1rem;
}
