input:-webkit-autofill:focus + .form__error,
input:-webkit-autofill:active + .form__error {
  font-weight: 300;
}

input:focus ~ label,
label:active,
input:focus ~ span {
  font-weight: 300;
}

.form {
  max-width: 530px;
  display: flex;
  flex-direction: row;
  gap: 0.75rem 2.5rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

input {
  height: auto;
}

form:focus span {
  font-weight: 300;
}

select {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  height: auto;
  border: 0;
  color: rgba(255, 255, 255, 1);
  width: 100%;
  padding: 0.5rem;
}

option {
  background: #a8a8a8;
}

label {
  display: flex;
  gap: 0.5rem;
  flex-direction: column;
  min-width: 40%;
  min-height: 85px;
}

.form__submit {
  width: 200px;
  font-size: 1.15rem;
  height: auto;
  margin: 0 auto;
  margin-top: 0.5rem;
  cursor: pointer;
  transition: 0.3s;
}

.form__submit:hover {
  background: rgba(255, 255, 255, 0.15);
}

.form__checkbox {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  min-height: 40px;
}

.form__checkbox .form__error {
  width: 100%;
  align-self: center;
  text-align: center;
  margin-top: -0.25rem;
}

.form__checkbox input {
  height: 1rem;
  width: 1rem;
  margin-top: 0.05rem;
}

.form__checkbox-label {
  display: flex;
  flex-direction: row;
  min-height: auto;
}

input.error {
  outline: solid 1px red;
}

.form__switch {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 40%;
}

.switch__title {
  padding-bottom: 0.5rem;
}

input:disabled {
  background: rgba(0, 0, 0, 0.2);
  color: #a8a8a8;
  cursor: not-allowed;
}

input:disabled:hover {
  background: rgba(0, 0, 0, 0.2);
}

.form__error {
  font-size: 0.75rem;
  color: red;
}

@media screen and (max-width: 1300px) {
  .form {
    margin: 0 auto;
  }
}

@media screen and (max-width: 500px) {
  label {
    min-width: 100%;
  }

  .form__switch {
    min-width: 100%;
  }

  .switch__title {
    align-self: flex-start;
  }
}
