.form-card {
  max-width: fit-content;
  display: flex;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  flex-direction: row;
  flex-wrap: wrap;
}

.form-card__props {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  flex-direction: column;
}

.form-card__info {
  padding: 1rem 2rem;
}
