@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap');

:root {
  --maintheme: #61dafb;
  --bg-color: #282c34;
  --white: #fff;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

input,
button,
textarea {
  font-family: inherit;
}

body {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: normal;
  color: var(--white);
  background: var(--bg-color);
  margin: 0;
  letter-spacing: 0.05rem;
}

#root {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

h1 {
  font-weight: normal;
  font-size: 1.5rem;
  color: var(--maintheme);
  margin: 1rem 2rem 1rem 1rem;
  white-space: nowrap;
}

h2 {
  margin: 1rem 0;
}

h3,
h4 {
  margin: 0.5rem 0;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
  color: inherit;
}

button {
  cursor: pointer;
}

a,
a:visited,
a:link {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  color: var(--maintheme);
}

nav,
footer,
header,
aside {
  display: block;
}

p {
  margin: 0.5rem 0;
}

.wrapper {
  max-width: 1300px;
  width: 95%;
  margin: 0 auto;
}

h1 {
  font-weight: normal;
  font-size: 1.5rem;
  color: var(--maintheme);
  margin: 1rem 2rem 1rem 1rem;
  white-space: nowrap;
}

.btn {
  max-width: 117px;
  border-radius: 4px;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 1rem;
  outline: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  -ms-flex-pack: center;
  padding: 6px 15px;
  -webkit-tap-highlight-color: transparent;
  position: relative;
  font-weight: 300;
  letter-spacing: 0.05rem;
}

a.btn {
  display: inline;
}

a.btn:hover {
  color: inherit;
}

a.btn.btn.inactive:hover {
  color: #a8a8a8;
}

.btn-big {
  max-width: 150px;
  background: rgba(157, 117, 194, 50%);
}

.btn:hover {
  opacity: 0.9;
}

.btn:active {
  opacity: 0.7;
}

.btn.active {
  background: rgba(0, 0, 0, 0.25);
}

.btn.inactive {
  background: rgba(0, 0, 0, 0.25);
  color: #a8a8a8;
  cursor: default;
}

.btn.inactive:active,
.btn.inactive:hover {
  opacity: 1;
}

.modal {
  position: fixed;
  left: 50%;
  top: 30%;
  transform: translate(-50%, -50%);
  transition: 0.5s;
  font-size: 1.5rem;
  color: var(--maintheme);
  background: rgba(0, 0, 0, 0.44);
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  font-weight: 500;
  opacity: 0;
  display: none;
}

ul,
li {
  line-height: 25px;
  list-style: none;
  padding: 0;
}

.hidden {
  display: none;
}

.hide {
  display: none;
}

@media screen and (max-width: 768px) {
  html {
    font-size: 14px;
  }

  > div,
  a,
  p {
    line-height: 1.5rem;
  }
}
